module.exports = "<div _v-0f16e3f9=\"\"> <div class=\"ui fixed inverted menu\" _v-0f16e3f9=\"\"> <div class=\"ui container\" _v-0f16e3f9=\"\"> <a href=# class=\"header item\" _v-0f16e3f9=\"\"> <img class=logo src=" + require("./assets/logo-mini.png") + " _v-0f16e3f9=\"\"> Bando Casa Emilia-Romagna </a> <div class=\"right item\" _v-0f16e3f9=\"\"> <a class=\"ui inverted button\" v-link=\"{ path: '/browser' }\" _v-0f16e3f9=\"\">Consulta alloggi <i class=\"right arrow icon\" _v-0f16e3f9=\"\"></i></a> </div> </div> </div> <div class=\"ui main text container\" _v-0f16e3f9=\"\"> <h1 class=\"ui header\" _v-0f16e3f9=\"\">Il bando della Regione Emilia-Romagna <div class=\"ui blue tag label\" _v-0f16e3f9=\"\">2015</div></h1> <h2 class=\"ui header\" _v-0f16e3f9=\"\">Una casa alle giovani coppie ed altri nuclei familiari</h2> <p _v-0f16e3f9=\"\">La Regione Emilia Romagna aiuta le giovani coppie e altri nuclei familiari nell'acquisto della prima casa mettendo a disposizione un contributo di <b _v-0f16e3f9=\"\">25.000,00 euro</b> o <b _v-0f16e3f9=\"\">30.0000,00 euro</b><i class=\"help circle icon moneyInfo\" data-content=\"Per i soggetti residenti nei Comuni della Regione interessati dagli eventi sismici del 20 e 29 maggio 2012 che acquistano, anche con patto di futura vendita, un alloggio nell'ambito dello stesso Comune di residenza o in un comune ad esso contermine ricompreso nell'elenco dei Comuni terremotati\" data-variation=\"very wide\" _v-0f16e3f9=\"\"></i> per alloggio.</p> <p _v-0f16e3f9=\"\">Gli alloggi disponibili si possono visualizzare nell'elenco pubblicato nel riquadro a lato \"consultazione lista alloggi\" sulla <a href=http://territorio.regione.emilia-romagna.it/bandi-di-gara/bando-giovani-coppie-e-altri-nuclei-familiari target=_blank _v-0f16e3f9=\"\">pagina ufficiale del bando</a> oppure <a v-link=\"{ path: '/browser' }\" _v-0f16e3f9=\"\">nel nostro applicativo</a>.</p> <p _v-0f16e3f9=\"\">Per maggiori informazioni consultare la <a href=http://territorio.regione.emilia-romagna.it/bandi-di-gara/bando-giovani-coppie-e-altri-nuclei-familiari target=_blank _v-0f16e3f9=\"\">pagina ufficiale del Bando giovani coppie</a> pubblicata dalla Regione per maggiori informazioni. </p> <h1 class=\"ui header\" _v-0f16e3f9=\"\">Visualizzazione alternativa degli alloggi</h1> <p _v-0f16e3f9=\"\">L'obiettivo di questo progetto è di offrire una visualizzazione interattiva per la ricerca degli alloggi interessati. Si tratta di un'applicazione web (accessibile <a v-link=\"{ path: '/browser' }\" _v-0f16e3f9=\"\">qui</a>) che mette a disposizione l'elenco degli alloggi pubblicato dalla Regione arricchito con la posizione geografica per ogni immobile.</p> <p _v-0f16e3f9=\"\">Per facilitare la ricerca e il confronto degli alloggi sono stati implementati diversi filtri, ordinamento e due modalità di visualizzazione: elenco o mappa.</p> <h3 class=\"ui header\" _v-0f16e3f9=\"\">Filtri</h3> Per facilitare la ricerca sono stati implementati i seguenti filtri: <div class=\"ui list\" _v-0f16e3f9=\"\"> <div class=item _v-0f16e3f9=\"\"> <i class=\"map icon\" _v-0f16e3f9=\"\"></i> <div class=content _v-0f16e3f9=\"\"> <div class=header _v-0f16e3f9=\"\">Ubicazione</div> <div class=description _v-0f16e3f9=\"\">Per molti la posizione dell'immobile è il fattore più importante nella ricerca della casa. Per specificare l'area di interesse è possibile scegliere la/le provincia/e e la/le città di proprio interesse oppure disegnare un'area sulla mappa.</div> </div> </div> <div class=item _v-0f16e3f9=\"\"> <i class=\"euro icon\" _v-0f16e3f9=\"\"></i> <div class=content _v-0f16e3f9=\"\"> <div class=header _v-0f16e3f9=\"\">Prezzo</div> <div class=description _v-0f16e3f9=\"\">E' possibile scegliere un intervallo di valori desiderati sia per il prezzo complessivo che per il prezzo a metro quadro.</div> </div> </div> <div class=item _v-0f16e3f9=\"\"> <i class=\"square outline icon\" _v-0f16e3f9=\"\"></i> <div class=content _v-0f16e3f9=\"\"> <div class=header _v-0f16e3f9=\"\">Dimensione</div> <div class=description _v-0f16e3f9=\"\">E' possibile scegliere un intervallo di valori desiderati per il numero di locali e per la superficie complessiva dell'immobile.</div> </div> </div> </div> <h3 class=\"ui header\" _v-0f16e3f9=\"\">Elenco</h3> <img class=\"ui centered big bordered rounded image\" src=" + require("./assets/listView.png") + " _v-0f16e3f9=\"\"> <h3 class=\"ui header\" _v-0f16e3f9=\"\">Mappa</h3> <img class=\"ui centered big bordered rounded image\" src=" + require("./assets/mapView.jpg") + " _v-0f16e3f9=\"\"> <div class=\"ui basic center aligned segment\" _v-0f16e3f9=\"\"> <div class=\"ui huge primary button\" v-link=\"{ path: '/browser' }\" _v-0f16e3f9=\"\">Avvia la visualizzazione<i class=\"right arrow icon\" _v-0f16e3f9=\"\"></i></div> </div> </div> <div class=\"ui inverted vertical footer segment\" _v-0f16e3f9=\"\"> <div class=\"ui container\" _v-0f16e3f9=\"\"> <div class=\"ui stackable inverted divided equal height stackable grid\" _v-0f16e3f9=\"\"> <div class=\"three wide column\" _v-0f16e3f9=\"\"> <h4 class=\"ui inverted header\" _v-0f16e3f9=\"\">Sviluppato da</h4> <img src=" + require("./assets/octavian.jpg") + " class=\"ui avatar tiny left floated image\" _v-0f16e3f9=\"\"> Octavian Bujor <div class=\"ui inverted link list\" _v-0f16e3f9=\"\"> <a class=\"ui inverted circular icon button\" href=https://github.com/obujor target=_blank _v-0f16e3f9=\"\"><i class=\"github icon\" _v-0f16e3f9=\"\"></i></a> <a class=\"ui inverted circular icon button\" href=https://it.linkedin.com/in/octavian-bujor-6b436790 target=_blank _v-0f16e3f9=\"\"><i class=\"linkedin icon\" _v-0f16e3f9=\"\"></i></a> </div> </div> <div class=\"three wide column\" _v-0f16e3f9=\"\"> <h4 class=\"ui inverted header\" _v-0f16e3f9=\"\">Link</h4> <div class=\"ui inverted link list\" _v-0f16e3f9=\"\"> <a href=http://www.regione.emilia-romagna.it/ class=item target=_blank _v-0f16e3f9=\"\">Regione Emilia-Romagna</a> <a href=http://territorio.regione.emilia-romagna.it/bandi-di-gara/bando-giovani-coppie-e-altri-nuclei-familiari class=item target=_blank _v-0f16e3f9=\"\">Bando 2015</a> <a href=http://territorio.regione.emilia-romagna.it/notizie/casagiov class=item target=_blank _v-0f16e3f9=\"\">Graduatorie 2015</a> </div> </div> <div class=\"ten wide column\" _v-0f16e3f9=\"\"> <h4 class=\"ui inverted header\" _v-0f16e3f9=\"\">Note legali </h4> <p _v-0f16e3f9=\"\">La fonte dei dati originali (le coordinate geografiche escluse) è la <a href=http://www.regione.emilia-romagna.it/ target=_blank _v-0f16e3f9=\"\">Regione Emilia-Romagna.</a></p> <p _v-0f16e3f9=\"\">Le coordinate geografiche sono state generate automaticamente usando diversi servizi di Geocoding quindi non si offre nessuna garanzia sulla loro accuratezza!</p> </div> </div> </div> </div> </div>";


